import React from "react";
import { Field } from "redux-form";
import styles from "./FormsControls.module.css";

const FormControl = ({ input, meta: {touched, error}, children }) => {//meta: {touched, error} ===  meta.touched && meta.error
    const hasError = touched && error;//meta.error   10 

    return (
        <div className={styles.formControl + " " + (hasError ? styles.error : " " /*  error  styles.error    */)}>
            <div>
                {children}
            </div>
            {hasError && <span>{error}</span> /*     ( 10 )   span*/}
        </div>
    )
}


export const Textarea = (props) => { // rest parametrs
    const { input, meta, child, ...restProps } = props;
    return <FormControl {...props}><textarea {...input} {...restProps} /></FormControl>
}

export const Input = (props) => {
    const { input, meta, child, ...restProps } = props;
    return (
        <FormControl {...props}><input {...input} {...restProps} /></FormControl>
    )
}

export const createField = (placeholder, name, validators, component, props = {}, text = " ") => (//    ();props = {}, text = " " -        
    <div>
        <Field placeholder={placeholder} name={name}
            validate={validators}
            component={component}
            {...props}
        /> {text}
    </div>//     Field - {text}
)

