import React, { Component } from 'react';
import './App.css';
import Navbar from './components/Navbar/Navbar';
import { BrowserRouter, Switch, Route, withRouter, Redirect } from "react-router-dom";
// import { BrowserRouter as Router } from 'react-router-dom';
import UsersContainer from './components/Users/UsersContainer';
import HeaderContainer from './components/Header/HeaderContainer';
import Login from './components/Login/Login';
import { initializeApp } from './redux/app-reducer'
import { connect, Provider } from 'react-redux';
import { compose } from 'redux';
import Preloader from './components/common/Preloader/Preloader';
import store from './redux/redux-store';
import { withSuspense } from './hoc/withSuspense';
const DialogsContainer = React.lazy(() => import('./components/Dialogs/DialogsContainer'));//    bundle() DialogsContainer   -  ''
const ProfileContainer = React.lazy(() => import('./components/Profile/ProfileContainer'));//    bundle() ProfileContainer   -  ''
//   React.lazy     (      ) App    

class App extends React.Component {

  catchAllUnhandledErrors = (reason, promise) => {
    alert("Some error occured");//   alert ( )
  }

  componentDidMount() {
    this.props.initializeApp();//  getAuthUserData    me(  ?)
    window.addEventListener("unhandledrejection", this.catchAllUnhandledErrors);//  window   -     -  (  catchAllUnhandledErrors) 
  }
  componentWillUnmount() {
    window.removeEventListener("unhandledrejection", this.catchAllUnhandledErrors);//!   addEventListener(componentDidMount)    removeEventListener(componentWillUnmount)!
  }

  render() {
    if (!this.props.initialized) {  //   
      return <Preloader />
    }
    return (

      <div className='app-wrapper'>
        <HeaderContainer />
        <Navbar />
        <div className='app-wrapper-content'>
          <Switch>
            <Route exact path='/' render={() => <Redirect to={'/profile'} />//        (    )
            } />
            <Route path='/dialogs' render={withSuspense(DialogsContainer)} />
            <Route path='/profile/:userId?' render={withSuspense(ProfileContainer)} />
            <Route path='/users' render={() => <UsersContainer />} />
            <Route path='/Login' render={() => <Login />} />
            <Route path='*' render={() => <div>404 NOT FOUND</div>//    URL     URL ( )(     URL  ) 
            } />
          </Switch>
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state) => ({
  initialized: state.app.initialized//     
})


let AppContainer = compose(
  withRouter,
  connect(mapStateToProps, { initializeApp }))(App);

const SamuraiJSApp = (props) => {
  return <BrowserRouter>
    <Provider store={store}>
      <AppContainer />
    </Provider>
  </BrowserRouter>
}

export default SamuraiJSApp;