import React from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { required } from "../../utils/validators/validators";
import { createField, Input } from "../common/FormsControls/FormsControls";
import { login } from "../../redux/auth-reducer";
import { Redirect } from "react-router-dom";
import styles from "./../common/FormsControls/FormsControls.module.css";


const LoginForm = ({ handleSubmit, error, captchaUrl }) => { //    props.handleSubmit...
    console.log('RERENDER')
    return (
        <form onSubmit={handleSubmit}>
            {createField('Email', "email", [required], Input) /*   placeholder, name, validate, component*/}
            {createField('Password', "password", [required], Input, { type: "password" }) /* {type: "password"}   - {...props}  createField*/}
            {createField(null, "rememberMe", [], Input, { type: "checkbox" }, "remember me") /* "remember me"  */}

            {captchaUrl && <img src={captchaUrl} />//  state        
            }
            {captchaUrl &&   createField("symbols from image", "captcha", [required], Input, {}) /* "remember me"  */}

            {error && <div className={styles.formSummaryError} /*   props  error    styles error */>
                {error}
            </div>}
            <div>
                <button>login</button>
            </div>
        </form>
    )
}

const LoginReduxForm = reduxForm({ form: 'login' })(LoginForm)
//reduxForm  hoc   LoginForm  store. dispathc action

const Login = (props) => {
    const onSubmit = (FormData) => {
        props.login(FormData.email, FormData.password, FormData.rememberMe, FormData.captcha)//  login   FormData     props  
    }

    if (props.isAuth) {
        return <Redirect to={"/profile"} />//     redirect
    }

    return <div>
        <h1>Login</h1>
        <LoginReduxForm onSubmit={onSubmit} captchaUrl={props.captchaUrl} />
    </div>
}
const mapStateToProps = (state) => ({
    captchaUrl: state.auth.captchaUrl,
    isAuth: state.auth.isAuth
})

export default connect(mapStateToProps, { login })(Login);//login    auth-reducer