import React, {useState} from 'react';
import styles from './Paginator.module.css';
import cn from  "classnames";

let Paginator = ({totalItemsCount, PageSize, currentPage, onPageChanged, portionSize = 10}) => {

    let pagesCount = Math.ceil(totalItemsCount / PageSize);//  (pagesCount) =    /    (10)( 10 )

    let pages = [];

    for (let i = 1; i <= pagesCount; i++) {
        pages.push(i);//      ; 
    }

    let portionCount = Math.ceil(pagesCount / portionSize);//    =  / (10)
    let [portionNumber, setPortionNumber] = useState(1);
    let leftPortionPageNumber = (portionNumber - 1) * portionSize + 1;//    -     (1 - 1) * 10 + 1 = 1 -        
    let rightPortionPageNumber = portionNumber * portionSize;//    - 1 * 10 = 10     10 



    return  <div className={styles.paginator}>
        {portionNumber > 1 &&
        <button onClick={() => {setPortionNumber(portionNumber - 1)}}>PREV</button>//  PREV     portionNumber  1 -  setPortionNumber(  ) =   - 1
        }
            {pages
            .filter(p => p >= leftPortionPageNumber && p<=rightPortionPageNumber)//   filter      -           (leftPortionPageNumber)       (rightPortionPageNumber)
            .map((p) => { 
                return <span className={ cn({
                    [styles.selectedPage]: currentPage === p 
                }, styles.pageNumber)}//c  cn (classnames)     ;styles.selectedPage           
                key={p}
                    onClick={(event) => { 
                        onPageChanged(p); 
                    }}>{p}</span>
            })}
            { portionCount > portionNumber &&
            <button onClick={() => {setPortionNumber(portionNumber + 1)}}>NEXT</button>//  NEXT  portionCount( )       
            }
        </div>
       
}

export default Paginator;