export const required = value => {
    if (value) return undefined; //    undefined
    return 'Field is required'; //     Field is required
     
}


export const MaxLengthCreator = (maxLenght) => value => { // MaxLengthCreator      value ()
    if (value.length > maxLenght) return `Max length is ${maxLenght} symbols`; //    maxLenght    Max length is 10 symbols
    return undefined; //     undefined
}